<template>
    <div class="addcontest">
        <div><router-link to="/contest"><el-button type="primary"></el-button></router-link></div>
        <div><span></span><el-input v-model="contestName"></el-input></div>
        <div><span></span><el-input type="textarea" :rows="8" v-model="contestAbs"></el-input></div>
        <div><span></span>
            <el-date-picker
            v-model="timeAll" value-format="yyyy-MM-dd"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
            </el-date-picker></div>
        <div><span></span><el-date-picker
            v-model="contestTime" value-format="yyyy-MM-dd"
            type="date"
            placeholder="">
            </el-date-picker></div>
        <div><el-button type="danger" @click="addContest"></el-button></div>
    </div>
</template>

<script>
import axios from 'axios'
axios.defaults.withCredentials = true
export default {
    inject:['reload'],
    data(){
        return{
            timeAll:'',
            contestTime:'',
            contestName:'',
            contestAbs:''
        }
    },
    methods:{
        addContest(){
            if(this.timeAll==''||this.contestTime==''||this.contestName==''||this.contestAbs==''){
                this.$alert('','',{
                callback: action=>{

                }
            })
            }else{
                for(let i = 0; i < this.contestTime.length; i++){
                    this.timeAll[0] = this.timeAll[0].replace(/-/g,'.')
                    this.timeAll[1] = this.timeAll[1].replace(/-/g,'.')
                    this.contestTime = this.contestTime.replace(/-/g,'.')
                }
                axios.post(this.GLOBAL.urlHead + '/manage/contest/addContest',{
                    contestName: this.contestName,
                    contestContext: this.contestAbs,
                    contestStartTime: this.timeAll[0],
                    contestEndTime: this.timeAll[1],
                    contestDuringTime: this.contestTime
                    }
                ).then(res=>{

                    if(res.data.code == 100001){
                        this.$alert('','',{
                            callback: action=>{
                                this.$router.go(-1)
                            }
                        })
                    }
                })
            }
        }
    }
}
</script>

<style scoped>
.addcontest{
    padding: 20px 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.addcontest div{
    width: 70%;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>



